
/**
 * @imports
 */
import {
	_isArray,
	_isBoolean,
	_isFunction,
	_isNumeric,
	_isObject,
	_isString,
	_isNull,
	_isTypeObject,
	_isObservable,
	_isUndefined,
} from './Js.js';
import {
	_from as _arr_from,
	_first as _arr_first,
	_last as _arr_last,
	_pushUnique,
	_rand,
} from './Arr.js';

/**
 * ---------------------------
 * Object utils
 * ---------------------------
 */				
 
/**
 * Return an object for the given pair(s) of input.
 *
 * @param string|array 			key
 * @param mixed|array			val 
 *
 * @return object
 */
const _from = function(key, val = null) {
	var obj = {};
	if (arguments.length === 2) {
		if (_isString(key)) {
			obj[key] = val;
		} else if (_isArray(key) && _isArray(val)) {
			key.forEach((k, i) => obj[k] = val[i]);
		}
	}
	return obj;
};
 
/**
 * Return the input object with additional entries.
 *
 * @param object	 			obj
 * @param string|array 			entriesOrKey
 * @param mixed|array			val 
 *
 * @return object
 */
const _with = function(obj, entriesOrKey, val = null) {
	return _merge(obj, _isObject(entriesOrKey) ? entriesOrKey : _from(entriesOrKey, val));
};
 
/**
 * Loops thru obj flatly with a callback function.
 * Stops when callback returns a non-undefined value.
 *
 * @param array|object 			obj 			The array or object to iterate.
 * @param function 				callback 		The callback function.
 *
 * @return mixed|null			Any non-null return from callback
 */
const _each = function(obj, callback) {
	var returnValue = undefined;
	if (_isTypeObject(obj)) {
		Object.keys(obj).forEach(k => {
			if (returnValue !== false) {
				returnValue = callback(_isNumeric(k) ? parseFloat(k) : k, obj[k]);
			}
		});
	}
	return returnValue;
};
	
/**
 * Retrieves the value at the given path.
 *
 * A return value of undefined is ambiguous, and can mean either that the
 * path does not exist, or that the path actually exists but with a value of undefined. If it is required to
 * know whether the path actually exists, pass an object as a third argument.
 * This object will have an "exists" key set to true/false
 *
 * @param object 				ctxt
 * @param array 				path
 * @param object 				reciever
 * @param object 				detectObservables
 *
 * @return mixed
 */
const _get = function(ctxt, path, reciever = {}, detectObservables = true) {
	path = _arr_from(path).slice();
	var _ctxt = ctxt;
	while(!_isUndefined(_ctxt) && !_isNull(_ctxt) && path.length) {
		var _key = path.shift();
		var isObsvble = detectObservables && _isObservable(_ctxt);
		if (!(isObsvble ? _ctxt.has(_key) : (_key in _ctxt))) {
			reciever.exists = false;
			return;
		}
		_ctxt = isObsvble ? _ctxt.get(_key) : _ctxt[_key];
	}
	reciever.exists = true;
	return _ctxt;
};
	
/**
 * Sets a value to the given path.
 *
 * @param object 				obj
 * @param array 				path
 * @param mixed 				val
 * @param object|function 		buildTree
 *
 * @return bool
 */
const _set = function(obj, path, val, buildTree = {}) {
	path = _arr_from(path);
	var target = obj;
	for(var i = 0; i < path.length; i ++) {
		if (i < path.length - 1) {
			if (!_isTypeObject(target) && !_isFunction(target)) {
				return false;
			}
			if (!_isTypeObject(target[path[i]])) {
				if (!buildTree) {
					return false;
				}
				target[path[i]] = _isFunction(buildTree) ? buildTree(i) : buildTree;
			}
			target = target[path[i]];
		} else {
			target[path[i]] = val;
			return true;
		}
	}
};
	
/**
 * Unsets a value from the given path.
 *
 * @param object 				obj
 * @param array 				path
 * @param bool|function 		reduceTree
 *
 * @return bool
 */
const _unset = function(obj, path, reduceTree = false) {
	path = _arr_from(path);
	var success = false;
	do {
		var lastKey = path.pop();
		var lastKey = lastKey === ''/*null*/ ? 0 : lastKey;
		var _obj = obj;
		if (path.length) {
			_obj = _get(obj, path);
		}
		if ((_isTypeObject(_obj) || _isFunction(_obj)) && lastKey in _obj) {
			delete _obj[lastKey];
			success = true;
		}
	} while (success && reduceTree && path.length && (_isTypeObject(_obj) || _isFunction(_obj)) && !Object.keys(_obj).length);
	return success;
};
	
/**
 * Tells if the given path exists.
 *
 * @param object 				obj
 * @param array 				path
 *
 * @return bool
 */
const _isset = function(obj, path) {
	var reciever = {};
	_get(obj, path, reciever);
	return reciever.exists;
};

/**
 * Returns the FIRST ENTRY.
 *
 * @param array|object 	obj
 *
 * @return mixed
 */
const _first = function(obj) {
	return _isArray(obj) ? _arr_first(obj) : obj[Object.keys(obj)[0]];
};

/**
 * Returns the LAST ENTRY.
 *
 * @param array|object 	obj
 *
 * @return mixed
 */
const _last = function(obj) {
	return _isArray(obj) ? _arr_last(obj) : obj[_arr_last(Object.keys(obj))];
};

/**
 * Returns a random subset of object.
 *
 * @param array|object 	obj
 * @param int		 	amount
 *
 * @return array|object
 */
const _sample = function(obj, amount = 1) {
	return _isArray(obj) ? _rand(obj) : _copy(obj, _rand(Object.keys(obj), amount));
};

/**
 * Finds a value in an array/object and returns the key (or path when found deep).
 *
 * @param array}object 	obj
 * @param function		callback
 * @param bool			deep
 *
 * @return number|string|array
 */
const _find = function(obj, callback, deep = false) {
	var keys = null;
	var values = obj;
	if (!_isArray(obj)) {
		keys = Object.keys(obj);
		values = Object.values(obj);
	}
	var subKey = undefined;
	var value = values.reduce((prev, curr) => {
		if (subKey === undefined) {
			if (callback(curr, prev)) {
				return curr;
			}
			if (deep && (_isTypeObject(curr) || _isFunction(curr)) && (subKey = _find(curr, callback, deep)) !== undefined) {
				return curr;
			}
		}
		return prev;
	}, undefined);
	if (value !== undefined) {
		var key = keys ? keys[values.indexOf(value)] : values.indexOf(value);
		return subKey !== undefined ? [key].concat(_arr_from(subKey)) : key;
	}
};

/**
 * Eagerly retrieves object members all down the prototype chain.
 *
 * @param object	 	obj
 * @param object	 	until
 *
 * @return array
 */
const _getAllPropertyNames = function(obj, until) {
	var keysAll = [];
	_getPrototypeChain(obj, until).forEach(obj => {
		_pushUnique(keysAll, ...Object.getOwnPropertyNames(obj));
	});
	return keysAll;
};

/**
 * Returns the prototype chain.
 *
 * @param object 		obj
 * @param object	 	until
 *
 * @return bool
 */
const _getPrototypeChain = function(obj, until) {
	until = until || Object.prototype;
	until = until && !_isArray(until) ? [until] : until;
	// We get the chain of inheritance
	var prototypalChain = [];
	var obj = obj;
	while((obj && (!until || until.indexOf(obj) < 0) && obj.name !== 'default')) {
		prototypalChain.push(obj);
		obj = obj ? Object.getPrototypeOf(obj) : null;
	}
	return prototypalChain;
};

/**
 * Copies only properties of an object.
 *
 * @param object	 	obj
 * @param array		 	only
 * @param array		 	except
 *
 * @return object
 */
const _copyPlain = function(obj, filter = []) {
	return _mergeCallback([{}, obj], (key, obj1, obj2) => {
		if (!_isFunction(obj2[key])) {
			return _isFunction(filter) ? filter(key) 
				: (_isArray(filter) && filter.length ? filter.indexOf(key) > -1 : true);
		}
	}, false/*deepProps*/, false/*isReplace*/, false/*withSymbols*/);
};

/**
 * Copies an object.
 *
 * @param object	 	obj
 * @param array		 	filter
 *
 * @return object
 */
const _copy = function(obj, filter = []) {
	var depth = 0;
	if (_isNumeric(arguments[0]) && _isTypeObject(arguments[1])) {
		depth = arguments[0];
		obj = arguments[1];
		filter = arguments[2] || [];
	}
	return _mergeCallback([depth, {}, obj], (key, obj1, obj2) => {
		return _isFunction(filter) ? filter(key) 
			: (_isArray(filter) && filter.length ? filter.indexOf(key) > -1 : true);
	}, false/*deepProps*/, false/*isReplace*/, false/*withSymbols*/);
};

/**
  * Merges values from subsequent arrays/objects first array/object;
  * optionally recursive
  *
  * @param array ...objs
  *
  * @return void
  */
const _merge = function(...objs) {
	return _mergeCallback(objs, (k, obj1, obj2) => {
		return true;
	});
};

/**
  * Merges values from subsequent arrays/objects into first array/object but only when not set;
  * optionally recursive
  *
  * @param array ...objs
  *
  * @return void
  */
const _inherit = function(...objs) {
	return _mergeCallback(objs, (key, obj1, obj2) => {
		if (_isArray(obj1) && _isArray(obj2)) {
			if (obj1.indexOf(obj2[key]) === -1) {
				return true;
			}
		} else if (!(key in obj1)) {
			return true;
		}
	});
};

/**
  * Replaces properties of first array/object with values from subsequent arrays/objects;
  * optionally recursive
  *
  * @param array ...objs
  *
  * @return void
  */
const _replace = function(...objs) {
	var replaceIntoEmptyObj = Object.keys(objs[0]).length === 0;
	return _mergeCallback(objs, (key, obj1, obj2, i) => {
		if ((key in obj1) || (replaceIntoEmptyObj && i === 0)) {
			return true;
		}
	}, false/*deepProps*/, true/*isReplace*/);
};

/**
 * Asserts (members of) the first value against (members of) subsequent values.
 * Assertion could be TRUE, FALSE, or custom.
 *
 * @param mixed 			obj1
 * @param mixed 			obj2
 * @param bool|function		assertion
 * @param int				depth
 *
 * @return bool
 */
const _even = function(obj1, obj2, assertion = true, depth = 1) {
	if ((_isArray(obj1) && _isArray(obj2) && obj1.length !== obj2.length)
	|| (_isObject(obj1) && _isObject(obj2) && Object.keys(obj1).length !== Object.keys(obj2).length)) {
		return false;
	}
	var result = _compareCallback(obj1, obj2, (v1, v2) => {
		if (depth > 0 && ((_isArray(v1) && _isArray(v2)) || (_isObject(v1) && _isObject(v2)))) {
			return _even(v1, v2, assertion, depth - 1);
		}
		return _isFunction(assertion) ? assertion(v1, v2) : (v1 === v2) === assertion;
	}, false/*netComparison*/, false/*contrast*/, true/*returnOnFirstFalse*/);
	return _isArray(result) ? result.length === obj1.length
		: (_isObject(result) ? Object.keys(result).length === Object.keys(obj1).length : result);
};

/**
 * Gets the match(es) between (members of) two objects;
 * optionally with custom assertion.
 *
 * @param mixed 			ob1
 * @param mixed 			obj2
 * @param string|function	assertion
 * @param bool				netMatch
 *
 * @return object|undefined
 */
const _match = function(obj1, obj2, assertion = true, netMatch = true) {
	return _compareCallback(obj1, obj2, (v1, v2) => {
		if ((_isArray(v1) && _isArray(v2)) || (_isObject(v1) && _isObject(v2))) {
			return _match(v1, v2, assertion, netMatch);
		}
		return _isFunction(assertion) ? assertion(v1, v2) : (v1 === v2) === assertion;
	}, netMatch/*netComparison*/);
};

/**
 * Gets the difference(s) between (members of) two objects;
 * optionally with custom assertion.
 *
 * @param mixed 			ob1
 * @param mixed 			obj2
 * @param string|function	assertion
 * @param bool				netDiff
 *
 * @return object|undefined
 */
const _diff = function(obj1, obj2, assertion = true, netDiff = true) {
	return _compareCallback(obj1, obj2, (v1, v2) => {
		if ((_isArray(v1) && _isArray(v2)) || (_isObject(v1) && _isObject(v2))) {
			return _diff(v1, v2, assertion, netDiff);
		}
		return _isFunction(assertion) ? assertion(v1, v2) : (v1 === v2) === assertion;
	}, netDiff/*netComparison*/, true/*contrast*/);
};

/**
 * Asserts (members of) the first value against (members of) subsequent values.
 * Assertion could be TRUE, FALSE, or custom.
 *
 * @param mixed 			obj1
 * @param mixed 			obj2
 * @param bool|function		assertion
 *
 * @return bool
 */
const _percentageMatch = function(obj1, obj2, assertion = true) {
	var result = _compareCallback(obj1, obj2, (v1, v2) => {
		if ((_isArray(v1) && _isArray(v2)) || (_isObject(v1) && _isObject(v2))) {
			return _percentageMatch(v1, v2, assertion);
		}
		return (_isFunction(assertion) ? assertion(v1, v2) : (v1 === v2) === assertion) ? 1 : 0;
	}, false/*netComparison*/, false/*contrast*/, true/*returnOnFirstFalse*/);
	var values = _isObject(result) ? Object.values(result) : result;
	return values.reduce((a, b) => a + b, 0) / values.length;
};

/**
 * Asserts (members of) the first value against (members of) subsequent values.
 * Assertion could be TRUE, FALSE, or custom.
 *
 * @param mixed 			obj1
 * @param mixed 			obj2
 * @param bool|function		assertion
 *
 * @return bool
 */
const _percentageDiff = function(obj1, obj2, assertion = false) {
	return 1 - _percentageMatch(obj1, obj2, assertion);
};

/**
  * Merges values from subsequent arrays/objects first array/object;
  * optionally recursive
  *
  * @param array ...objs
  *
  * @return void
  */
const _mergeCallback = function(objs, callback, deepProps = false, isReplace = false, withSymbols = false) {
	var depth = 0;
	var obj1 = objs.shift();
	if (_isNumeric(obj1) || obj1 === true || obj1 === false) {
		depth = obj1;
		obj1 = objs.shift();
	}
	if (!objs.length) {
		throw new Error('_merge() requires two or more array/objects.');
	}
	objs.forEach((obj2, i) => {
		if (!_isTypeObject(obj2) && !_isFunction(obj2)) {
			return;
		}
		(deepProps ? _getAllPropertyNames(obj2) : Object.getOwnPropertyNames(obj2)).forEach(key => {
			var valAtObj1 = obj1[key];
			var valAtObj2 = obj2[key];
			if (((_isArray(valAtObj1) && _isArray(valAtObj2)) || (_isObject(valAtObj1) && _isObject(valAtObj2))) 
			&& (depth === true || depth > 0)) {
				// RECURSE...
				obj1[key] = _isArray(valAtObj1) && _isArray(valAtObj2) ? [] : {};
				_mergeCallback([_isNumeric(depth) ? depth - 1 : depth, obj1[key], valAtObj1, valAtObj2], callback, deepProps, isReplace);
			} else if (callback(key, obj1, obj2, i)) {
				if (_isArray(obj1) && _isArray(obj2)) {
					if (isReplace) {
						obj1[key] = valAtObj2;
					} else {
						obj1.push(valAtObj2);
					}
				} else if (withSymbols) {
					Object.defineProperty(obj1, key, Object.getOwnPropertyDescriptor(obj2, key));
				} else {
					obj1[key] = obj2[key];
				}
			}
		});
	});
	return obj1;
};

/**
 * Gets the match(es) between (members of) two values;
 * assertion optionally custom.
 *
 * @param mixed 			ob1
 * @param mixed 			obj2
 * @param string|function	assertion
 * @param bool				netComparison
 * @param bool				contrast
 * @param bool				returnOnFirstFalse
 *
 * @return bool|array|object
 */
const _compareCallback = function(obj1, obj2, assertion = true, netComparison = true, contrast = false, returnOnFirstFalse = false) {
	if (_isArray(obj1) && _isArray(obj2)) {
		var result = [];
		var contn = true;
		obj1.forEach(v1 => {
			if (!contn) {
				return;
			}
			var testPass = false;
			_each(obj2, (k, v2) => {
				if (!testPass || (netComparison && _isTypeObject(v1))) {
					testPass = assertion(v1, v2);
					if ((_isArray(testPass) && !testPass.length) || (_isObject(testPass) && !Object.keys(testPass).length)) {
						testPass = false;
					}
					if (_isTypeObject(testPass) && netComparison) {
						// Further recursions should use this testPass as v1
						v1 = testPass;
					}
				}
			});
			if (_isTypeObject(testPass)) {
				result.push(netComparison ? testPass : v1);
			} else if (!_isBoolean(testPass)) {
				result.push(testPass);
			} else if ((contrast && !testPass) || (!contrast && testPass)) {
				result.push(v1);
			} else if (returnOnFirstFalse) {
				contn = false;
			}
		});
		return result;
	}
	
	if (_isObject(obj1) && _isObject(obj2)) {
		var result = {};
		var contn = true;
		Object.keys(obj1).forEach(k => {
			if (!contn) {
				return;
			}
			var testPass = assertion(obj1[k], obj2[k]);;
			if ((_isArray(testPass) && !testPass.length) || (_isObject(testPass) && !Object.keys(testPass).length)) {
				testPass = false;
			}
			if (_isTypeObject(testPass)) {
				result[k] = netComparison ? testPass : obj1[k];
			} else if (!_isBoolean(testPass)) {
				result[k] = testPass;
			} else if ((contrast && !testPass) || (!contrast && testPass)) {
				result[k] = obj1[k];
			} else if (returnOnFirstFalse) {
				contn = false;
			}
		});
		return result;
	}
};

/**
 * @exports
 */
export {
	_from,
	_with,
	_each,
	_get,
	_set,
	_unset,
	_isset,
	_first,
	_last,
	_sample,
	_find,
	_getAllPropertyNames,
	_getPrototypeChain,
	_copyPlain,
	_copy,
	_merge,
	_inherit,
	_replace,
	_even,
	_match,
	_diff,
	_percentageMatch,
	_percentageDiff,
	_mergeCallback,
	_compareCallback,
};