
/**
 * @imports
 */
import {
	_all,
	_last,
	_slice
} from './Arr.js';
import {
	_each,
	_merge,
	_mergeCallback
} from './Obj.js';

/**
 * ---------------------------
 * Typechecks and other utils
 * ---------------------------
 */
 
/**
 * Tells if val is of type "string".
 *
 * @param string 	val
 *
 * @return bool
 */
const _isString = function(val) {
	return typeof val === 'string';
};
 
/**
 * Tells if val is of type "number".
 *
 * @param string 	val
 *
 * @return bool
 */
const _isNumber = function(val) {
	return typeof val === 'number';
};
 
/**
 * Tells if val is of type "string" or a numeric string.
 * This holds true for both numbers and numeric strings.
 *
 * @param string 	val
 *
 * @return bool
 */
const _isNumeric = function(val) {
	return val !== true && val !== false && val !== null && val !== '' && !isNaN(val * 1);
};

/**
 * Tells if val is of type "array".
 *
 * @param object	 	val
 *
 * @return bool
 */
const _isArray = function(val) {
	return Array.isArray(val);
};

/**
 * Tells if val is "array-like".
 * This holds true for anything that has a length property.
 *
 * @param object	 	val
 *
 * @return bool
 */
const _isTypeArray = function(val) {
	return !_isString(val) && !_isUndefined(val.length);
};

/**
 * Tells if val is pure object.
 *
 * @param object	 	val
 *
 * @return bool
 */
const _isObject = function(val) {
	return !Array.isArray(val) && typeof val === 'object' && val;
};

/**
 * Tells if val is of type "object".
 * This holds true for anything object, including built-ins.
 *
 * @param object	 	val
 *
 * @return bool
 */
const _isTypeObject = function(val) {
	return Array.isArray(val) || typeof val === 'object';
};
 
/**
 * Tells if val is of type "function".
 *
 * @param object 		val
 *
 * @return bool
 */
const _isFunction = function(val) {
	return _isTypeFunction(val) || (val && {}.toString.call(val) === '[object function]');
};

/**
 * Tells if val is of type "function".
 * This holds true for both regular functions and classes.
 *
 * @param object	 	val
 *
 * @return bool
 */
const _isTypeFunction = function(val) {
	return typeof val === 'function';
};
 
/**
 * Tells if val is of type "class".
 *
 * @param object 		val
 *
 * @return bool
 */
const _isClass = function(val) {
	return _isTypeFunction(val) && /^class\s/.test(Function.prototype.toString.call(val));
};
 
/**
 * Tells if val is undefined or is of type "undefined".
 *
 * @param string 	val
 *
 * @return bool
 */
const _isUndefined = function(val) {
	return arguments.length && (val === undefined || typeof val === 'undefined');
};
 
/**
 * Tells if val is undefined or is of type "boolean".
 *
 * @param string 	val
 *
 * @return bool
 */
const _isBoolean = function(val) {
	return val === true || val === false;
};
 
/**
 * Tells if val is undefined or is null.
 *
 * @param string 	val
 *
 * @return bool
 */
const _isNull = function(val) {
	return val === null || val === '';
};
 
/**
 * Tells if val is empty in its own type.
 * This holds true for NULLs, UNDEFINED, FALSE, 0,
 * objects without keys, empty arrays.
 *
 * @param string 	val
 *
 * @return bool
 */
const _isEmpty = function(val) {
	return _isNull(val) || _isUndefined(val) || val === false || val === 0 
		|| (_isTypeObject(val) && !Object.keys(val).length);
};
 
/**
 * Returns the val's type.
 *
 * @param string 	val
 *
 * @return string
 */
const _getType = function(val) {
	return typeof val;
};

/**
 * Tells if a given object has observability methods.
 *
 * @param mixed		input
 *
 * @return bool
 */
const _isObservable = function(input) {
	return _isObject(input) && _all([
		'observe',
		'unobserve',
		'setOffsetParent',
		'getOffsetParent',
	], method => _isFunction(input[method]));
};

/**
 * Creats a proxy and keeps special reference to it.
 *
 * @param object	 target
 * @param object	 trap
 *
 * @return Proxy
 */
const _proxy = function(target, trap) {
	var trapByCommons = {
		get: (target, prop) => {
			if (prop === '__proxyTargetByCommons') {
				return target;
			}
			if (prop === '__proxyTrapByCommons') {
				return trap;
			}
			return trap.get ? trap.get(target, prop) : target[prop];
		},
	};
	return new Proxy(target, _merge({}, trap, trapByCommons));
};

/**
 * Tells if a given object instance has been proxied.
 *
 * @param mixed		input
 *
 * @return bool
 */
const _isProxy = function(input) {
	return (_isObject(input) && input.__proxyTargetByCommons) || false;
};

/**
 * Returns the proxy's target object as earlier saved.
 *
 * @param mixed		proxy
 *
 * @return mixed
 */
const _getProxyTarget = function(instance) {
	return _isProxy(instance) ? instance.__proxyTargetByCommons : undefined;
};

/**
 * Returns the proxy's trap object as earlier saved.
 *
 * @param mixed		proxy
 *
 * @return mixed
 */
const _getProxyTrap = function(instance) {
	return _isProxy(instance) ? instance.__proxyTrapByCommons : undefined;
};

/**
 * A multi-inheritance implementstion.
 *
 * @param array	 	...classes
 *
 * @return object
 */
const _mixin = function(...classes) {
	var Traps = {};
	var RetrnDirective = 'last';
	if (_isArray(arguments[0])) {
		classes = arguments[0];
		Traps = arguments[1];
		if (arguments[2]) {
			RetrnDirective = arguments[2];
		}
	}
	var Base = _last(classes);
	var supersMap = {};
	var Mixin = class extends Base {
		constructor(...args) {
			super(...args);
		}
	};
	// A trap for _instanceof()
	Mixin.prototypes = classes;
	// ---------------------
	// Extend (merge) properties but keep methods
	classes.forEach(_class => {
		// Copy const members
		_mergeCallback([Mixin, _class], (key, obj1, obj2) => ['name', 'prototype', 'prototypes', 'length'].indexOf(key) === -1);
		_mergeCallback([Mixin.prototype, _class.prototype], (key, obj1, obj2) => {
			if (['prototype', 'prototypes'].indexOf(key) === -1) {
				if (_isFunction(obj2[key])) {
					if (_isArray(supersMap[key])) {
						supersMap[key].push(obj2[key]);
					} else {
						supersMap[key] = [obj2[key]];
					}
					return false;
				}
				return true;
			}
			return false;
		}, true/*deepProps*/);
	});
	// Extend (proxy) methods
	_each(supersMap, (name, supers) => {
		if (name === 'constructor') {
			return;
		}
		// NOTE: this must not be defined as an arrow function
		// for the benefit of the "this".
		Mixin.prototype[name] = function(...args) {
			if (Object.hasOwnProperty(Traps, name) && _isFunction(Traps[name])) {
				// Wrap a call to the trap...
				// So mixin supers are passed to traps
				return Traps[name].call(this, supers, ...args);
			} else {
				// Call each super and return
				// the last one's return value
				var supersReturnValues = [];
				supers.forEach(supr => {
					supersReturnValues.push(supr.call(this, ...args));
				})
				return _last(supersReturnValues);
			}
		};
	});
	return Mixin;
};

/**
 * Instanceof that supports our multi-inheritance implementstion.
 *
 * @param object	 	obj1
 * @param object	 	classB
 *
 * @return bool
 */
const _instanceof = function(obj, classB) {
	if (!obj) {
		return false;
	}
	if (obj instanceof classB) {
		return true;
	}
	var mixinTest = classA => {
		while (classA && classA !== Function.prototype) {
			if (classA === classB || (classA.prototypes && classA.prototypes.reduce((prevAns, prototype) => prevAns || (prototype === classB) || mixinTest(prototype), false))) {
				return true;
			}
			classA = Object.getPrototypeOf(classA);
		}
		return false;
	};
	return mixinTest(obj.constructor);
};

/**
 * @exports
 */
export {
	_isString,
	_isNumber,
	_isNumeric,
	_isArray,
	_isTypeArray,
	_isObject,
	_isTypeObject,
	_isFunction,
	_isTypeFunction,
	_isClass,
	_isUndefined,
	_isBoolean,
	_isNull,
	_isEmpty,
	_getType,
	_isObservable,
	_isProxy,
	_getProxyTarget,
	_getProxyTrap,
	_proxy,
	_mixin,
	_instanceof
};