
/**
 * ---------------------------
 * String utils
 * ---------------------------
 */

/**
 * Tells if the string is warapped with the given opening and closing tags.
 *
 * @param  string  subject
 * @param  string  openingTag
 * @param  string  closingTag
 *
 * @return bool
 */
const _wrapped = function(subject, openingTag, closingTag) {
	return subject.startsWith(openingTag) && subject.endsWith(closingTag);
};

/**
 * Returns the string without the given opening and closing tags.
 *
 * @param  string  subject
 * @param  string  openingTag
 * @param  string  closingTag
 *
 * @return string
 */
const _unwrap = function(subject, openingTag, closingTag) {
	return _beforeLast(_after(subject, openingTag), closingTag);
};

/**
 * Return the part of a string before a given value.
 *
 * @param  string  subject
 * @param  string  search
 * @param  bool	   beforeLast
 *
 * @return string
 */
const _before = function(subject, search, beforeLast = false) {
	if (search == '') {
		return subject;
	}
	var pos = beforeLast ? subject.lastIndexOf(search) : subject.indexOf(search);
	if (pos === -1) {
		return subject;
	}
	return subject.substr(0, pos);
};

/**
 * Return the part of a string before last occurence of a given value.
 *
 * @param  string  subject
 * @param  string  search
 *
 * @return string
 */
const _beforeLast = function(subject, search) {
	return _before(subject, search, true);
};

/**
 * Return the remainder of a string after a given value.
 *
 * @param  string  subject
 * @param  string  search
 * @param  bool	   afterLast
 *
 * @return string
 */
const _after = function(subject, search, afterLast = false) {
	if (search == '') {
		return subject;
	}
	var pos = afterLast ? subject.lastIndexOf(search) : subject.indexOf(search);
	if (pos === -1) {
		return '';
	}
	return subject.substr(pos + search.length);
};

/**
 * Return the remainder of a string after last occurence of a given value.
 *
 * @param  string  subject
 * @param  string  search
 *
 * @return string
 */
const _afterLast = function(subject, search) {
	return _after(subject, search, true);
}
 
/**
 * Converts a string to title case.
 *
 * @param string 	str
 * @param bool 		strict
 *
 * @return string
 */
const _toTitle = function(str, strict) {
	if (typeof str !== 'string') {
		return str;
	}
	return str.replace(/\w\S*/g,  function(txt) { return txt.charAt(0).toUpperCase() + ((typeof strict !== undefined && strict) ? txt.substr(1).toLowerCase() : txt.substr(1)); })
};

/**
 * Makes a string camel-cased.
 *
 * @param string 	str
 * @param bool	 	fromStart
 *
 * @return string
 */
const _toCamel = function(str, fromStart) {
	// Make disting words
	str = _toTitle(str.replace(/-/g, ' ')).replace(/ /g, '');
	return fromStart ? str : str[0].toLowerCase() + str.substr(1);
};

/**
 * Splits a camel-cased string.
 *
 * @param string 	str
 * @param string 	delimiter
 *
 * @return string
 */
const _fromCamel = function(str, delimiter) {
	return str === undefined ? '' : str.split(/(?=[A-Z])/).join(delimiter ? delimiter : ' '); // positive lookahead to keep the capital letters
};

