
/**
 * @imports
 */
import * as _Arr from './Arr.js';
import * as _Js from './Js.js';
import * as _Num from './Num.js';
import * as _Obj from './Obj.js';
import * as _Str from './Str.js';

/**
 * Standardize functions as
 * prototype methods.
 */
const standardize = (a, prototype, prefix = '') => Object.keys(a).reduce((prototype, funcName) => _Obj._with(prototype, prefix + funcName, function() {
	return a[funcName](this, ...arguments);
}), prototype);

/**
 * Deliver functions without
 * their initial underscore.
 */
const rename = (a, b = {}) => Object.keys(a).reduce((b, funcName) => _Obj._with(b, funcName.substr(1), a[funcName]), b);
const Arr = rename(_Arr);
const Js = rename(_Js);
const Num = rename(_Num);
const Obj = rename(_Obj);
const Str = rename(_Str);

/**
 * @exports
 */
export {Arr, Js, Num, Obj, Str, standardize};