
/**
 * @imports
 */
import {
	_isArray,
	_isBoolean,
	_isEmpty,
	_isNumber,
	_isNumeric,
	_isObject,
	_isString,
	_isTypeArray,
	_isUndefined
} from './Js.js';
import {
	_each
} from './Obj.js';

/**
 * ---------------------------
 * Array utils
 * ---------------------------
 */				

/**
 * Casts an array-like object to an array.
 *
 * @param mixed 	val
 *
 * @return array
 */
const _from = function(val) {
	if (_isArray(val)) {
		return val;
	};
	if (val !== false && val !== 0 && _isEmpty(val)) {
		return [];
	};
	if (_isTypeArray(val)) {
		return Array.prototype.slice.call(val);
	};
	if (_isObject(val)) {
		return Object.values(val);
	};
	return [val];
};

/**
 * Returns THE FIRST ENTRY OR A NUMBER OF ENTRIES counting forward from the begining.
 *
 * @param array 	arr
 * @param int	 	amount
 *
 * @return mixed|array
 */
const _first = function(arr, amount = 1) {
	var count = 0;
	arr.forEach(itm => {
		count ++;
	});
	var firsts = arr.slice(arr.length - count, amount);
	return arguments.length > 1 ? firsts : firsts[0];
};

/**
 * Returns THE LAST ENTRY OR A NUMBER OF ENTRIES counting forward to the end.
 *
 * @param array 	arr
 * @param int	 	amount
 *
 * @return mixed|array
 */
const _last = function(arr, amount = 1) {
	return arguments.length > 1
		? _first(arr.slice().reverse(), amount).reverse()
		: _first(arr.slice().reverse());
};

/**
 * Gets the average of an array of numbers.
 *
 * @param array 	arr
 *
 * @return number
 */
const _avg = function(arr) {
	return arr.length ? _sum(arr) / arr.length : 0;
};

/**
 * Gets the maximum of an array of numbers.
 *
 * @param array 	arr
 *
 * @return number
 */
const _max = function(arr) {
	arr = arr.slice();
	return arr.reduce((v1, v2) => Math.max(v1, v2), arr.shift());
};

/**
 * Gets the minimum of an array of numbers.
 *
 * @param array 	arr
 *
 * @return number
 */
const _min = function(arr) {
	arr = arr.slice();
	return arr.reduce((v1, v2) => Math.min(v1, v2), arr.shift());
};

/**
 * Gets one or more random values from an array.
 *
 * @param array 	arr
 * @param int	 	amount
 *
 * @return mixed|array
 */
const _rand = function(arr, amount = 1) {
	var result = [];
	var rand = null;
	while (result.length < amount && (rand = arr[Math.floor(Math.random() * arr.length)]) && result.indexOf(rand) === -1) {
		result.push(rand);
	};
	return arguments.length > 1 ? result : result[0];
};

/**
 * Sums an array of numbers.
 *
 * @param array 	arr
 *
 * @return number
 */
const _sum = function(arr) {
	arr = arr.slice();
	return arr.reduce((total, v) => total + v, arr.shift());
};

/**
 * Array slicing with support for negative offsets.
 *
 * @param array	 				arr
 * @param array	 				offset
 * @param array	 				lengthOrOffset2
 *	 *
 * @return array
 */
const _slice = function(arr, offset = 0, lengthOrOffset2 = null) {
	if (arguments.length > 1) {
		offset = offset < 0 ? (arr.length + offset)/*subtraction eventually*/ : offset;
		if (arguments.length > 2) {
			lengthOrOffset2 = lengthOrOffset2 < 0 ? (arr.length + lengthOrOffset2)/*subtraction eventually*/ - offset : lengthOrOffset2;
		};
	};
	return arr.slice(offset, lengthOrOffset2);
};

/**
 * 1. Returns the ENTRY preceding (either the FIRST or the LAST instance of) the reference.
 * 2. Returns A NUMBER OF ENTRIES counting backwards from (either the FIRST or the LAST instance of) the given reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param int|bool 	length
 * @param bool	 	rotate
 * @param bool	 	lastReference
 *
 * @return mixed|array
 */
const _preceding = function(arr, reference, length = false, rotate = false, lastReference = false) {
	if (arr.indexOf(reference) === -1) {
		return length ? [] : undefined;
	}
	var amount = length === true ? arr.length - 1 : (length === false ? 1 : length);
	var arr = arr.slice().reverse();
	var from = lastReference ? arr.lastIndexOf(reference) + 1 : arr.indexOf(reference) + 1;
	var before = !_isUndefined(reference) ? arr.slice(from, from + amount) : [];
	if (rotate && before.length < amount && before.length < arr.length) {
		before = before.concat(arr.slice(0, amount - before.length));
	};
	return length ? before : before[0];
};
	
/**
 * 1. Returns the ENTRY following (either the FIRST or the LAST instance of) the reference.
 * 2. Returns A NUMBER OF ENTRIES counting forwards from (either the FIRST or the LAST instance of) the given reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param int|bool 	length
 * @param bool	 	rotate
 * @param bool	 	lastReference
 *
 * @return mixed|array
 */
const _following = function(arr, reference, length = false, rotate = false, lastReference = false) {
	if (arr.indexOf(reference) === -1) {
		return length ? [] : undefined;
	}
	var amount = length === true ? arr.length - 1 : (length === false ? 1 : length);
	var from = lastReference ? arr.lastIndexOf(reference) + 1 : arr.indexOf(reference) + 1;
	var after = !_isUndefined(reference) ? arr.slice(from, from + amount) : [];
	if (rotate && after.length < amount && after.length < arr.length) {
		after = after.concat(arr.slice(0, amount - after.length));
	};
	return length ? after : after[0];
};

/**
 * Alias of _preceding() but uses last instance of reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param int|bool 	length
 * @param bool	 	rotate
 *
 * @return mixed|array
 */
const _precedingLast = function(arr, reference, length = false, rotate = false) {
	return _preceding(arr, reference, length, rotate, true/*lastReference*/);
};

/**
 * Alias of _following() but uses last instance of reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param int|bool 	length
 * @param bool	 	rotate
 *
 * @return mixed|array
 */
const _followingLast = function(arr, reference, length = false, rotate = false) {
	return _following(arr, reference, length, rotate, true/*lastReference*/);
};

/**
 * Returns ALL THE ENTRIES from (either the FIRST or the LAST instance of) the given reference UNTIL reference2,
 * in ltr/rtl direction.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param mixed	 	reference2
 * @param bool	 	rtl
 * @param bool	 	rotate
 * @param bool	 	lastReference
 *
 * @return array
 */
const _between = function(arr, reference, reference2, rtl = false, rotate = false, lastReference = false) {
	var from = lastReference ? arr.lastIndexOf(reference) : arr.indexOf(reference);
	var to = lastReference ? arr.lastIndexOf(reference2) : arr.indexOf(reference2);
	if (rtl) {
		var length = to > from 
			? from/*the begining backward*/ + arr.length - to/*the other half*/
			: from - to;
		return _preceding(arr, reference, length, rotate, lastReference);
	};
	var length = to < from 
		? arr.length - from - 1/*the remainder forward*/ + to + 1/*the other half*/
		: to - from;
	return _following(arr, reference, length, rotate, lastReference);
};

/**
 * Returns the intersection of two arrays;
 * optionally using a custom matching function.
 *
 * @param array 	arr
 * @param array	 	arr2
 * @param function 	callback
 *
 * @return array
 */
const _intersect = function(arr, arr2, callback = null) {
	return !_isArray(arr2) ? [] : arr.filter(val1 => callback 
		? arr2.filter(val2 => callback(val1, val2)).length 
		: arr2.indexOf(val1) !== -1
	);
};

/**
 * Returns the difference of two arrays;
 * optionally using a custom matching function.
 *
 * @param array 	arr
 * @param array	 	arr2
 * @param function 	callback
 *
 * @return array
 */
const _difference = function(arr, arr2, callback = null) {
	return !_isArray(arr2) ? [] : arr.filter(val1 => callback 
		? arr2.filter(val2 => callback(val1, val2)).length 
		: arr2.indexOf(val1) === -1
	);
};

/**
 * Returns a list of items that pass a callback test.
 *
 * @param array	 				arr
 * @param function				callback
 *	 *
 * @return array
 */
const _filter = function(arr, callback) {
	return _divide(arr, callback)[0];
};

/**
 * Makes a separation between items that pass a callback test and those that fail.
 *
 * @param array	 				arr
 * @param function				callback
 *	 *
 * @return array
 */
const _divide = function(arr, callback) {
	var passes = [];
	var failures = [];
	var length = arr.length;
	for (i = 0; i < length; i++) {
		if (callback(arr[i])) {
			passes.push(arr[i]);
		} else {
			failures.push(arr[i]);
		};
	};
	return [passes, failures];
};

/**
 * Flattens a nested array to the given depth.
 *
 * @param array 	arr
 * @param int 	 	depth
 * @param bool 	 	withObjects
 *
 * @return array
 */
const _flatten = function(arr, depth = 1, withObjects = true) {
	if (!_isNumeric(depth) || depth <= 0) {
		return arr;
	};
	if (!_isArray(arr) && _isObject(arr) && withObjects) {
		arr = Object.values(arr);
	};
	if (!_isArray(arr)) {
		return arr;
	};
	return arr.reduce((acc, val) => _isArray(val) || (_isObject(val) && withObjects) 
		? acc.concat(_flatten(!_isArray(val) ? Object.values(val) : val, depth - 1, withObjects)) 
		: acc.concat(val), []);
};

/**
 * Sorts a list performantly.
 *
 * @param array	 					arr
 * @param string	 				order			ASC|DESC 
 * @param function					callback
 *
 * @return array
 */
const _sort = function(arr, order, callback = null) {
	var _arr = [];
	// Make a shallow copy
	var length = arr.length;
	for (var i = 0; i < length; i ++) {
		_arr.push({index: i, value: callback ? callback(arr[i]) : arr[i]});
	};
	_arr.sort(function(a, b) {
		// Using localeCompare if possible
		if (_isString(a.value) && "".localeCompare) {
			return a.value.localeCompare(b.value);
		};
		return a.value === b.value ? 0 : a.value > b.value ? 1 : -1;
	});
	if ((order || '').trim().toLowerCase() === 'desc') {
		_arr = _arr.reverse();
	};
	// Fill the order with actual values
	return _arr.map(itm => arr[itm.index]);
};

/**
 * Returns a list of unique items.
 *
 * @param array	 				arr
 *	 *
 * @return array
 */
const _unique = function(arr) {
	const distinct = (value, index, self) => {
		return self.indexOf(value) === index;
	};
	return arr.filter(distinct);
};

/**
 * Adds an item if not already exist.
 *
 * @param array 	arr
 * @param array	 	...itms
 *
 * @return array
 */
const _pushUnique = function(arr, ...items) {
	items.forEach(itm => {
		if (arr.indexOf(itm) < 0) {
			arr.push(itm);
		}
	});
	return arr;
};

/**
 * Adds items that do not already exist.
 *
 * @param array 	arr
 * @param array	 	...arrs
 *
 * @return array
 */
const _concatUnique = function(arr, ...arrs) {
	arrs.forEach(_arr => {
		_arr.forEach(itm => _pushUnique(arr, ..._from(_arr)));
	});
	return arr;
};

/**
 * Removes instances of reference up to <limit> times.
 *
 * @param array 	arr
 * @param mixed	 	itm
 * @param int|bool 	limit
 *
 * @return array
 */
const _remove = function(arr, itm, limit = false) {
	var i = arr.indexOf(itm);
	while (i > -1 && (limit || limit === false)) {
		arr.splice(i, 1);
		if (limit > 0) {
			limit --;
		};
		i = arr.indexOf(itm);
	};
	return arr;
};

/**
 * Removes all instances of each item.
 *
 * @param array 	arr
 * @param array	 	itms
 *
 * @return array
 */
const _exclude = function(arr, ...itms) {
	itms.forEach(itm => _remove(arr, itm));
	return arr;
};

/**
 * Removes an item.
 *
 * @param array 	arr
 * @param array	 	itms
 * @param array	 	replacements
 *
 * @return array
 */
const _replace = function(arr, itms, replacements = []) {
	if (arguments.length === 2) {
		itms.forEach((itm, i) => {arr[i] = itm;});
		return arr;
	};
	_each(itms, (k, reference) => {
		var i = arr.indexOf(reference);
		if (i !== -1) {
			arr[i] = replacements[k];
		};
	});
	return arr;
};

/**
 * Adds A LIST OF ITEMS before the FIRST instance of the given reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param array	 	itms
 * @param bool	 	lastReference
 *
 * @return array
 */
const _concatBefore = function(arr, reference, itms, lastReference = false) {
	var secondHalf = arr.splice(lastReference ? arr.lastIndexOf(reference) : arr.indexOf(reference));
	itms.concat(secondHalf).forEach(function(itm) {
		arr.push(itm);
	});
	return arr;
};

/**
 * Adds A LIST OF ITEMS after the FIRST instance of the given reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param array	 	itms
 * @param bool	 	lastReference
 *
 * @return array
 */
const _concatAfter = function(arr, reference, itms, lastReference = false) {
	var secondHalf = arr.splice(lastReference ? arr.lastIndexOf(reference) + 1 : arr.indexOf(reference) + 1);
	itms.concat(secondHalf).forEach(function(itm) {
		arr.push(itm);
	});
	return arr;
};

/**
 * Adds A LIST OF ITEMS before the LAST instance of the given reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param array	 	itms
 *
 * @return array
 */
const _concatBeforeLast = function(arr, reference, itms) {
	return _concatBefore(arr, reference, itms, true/*lastReference*/);
};

/**
 * Adds A LIST OF ITEMS after the LAST instance of the given reference.
 *
 * @param array 	arr
 * @param mixed	 	reference
 * @param array	 	itms
 *
 * @return array
 */
const _concatAfterLast = function(arr, reference, itms) {
	return _concatAfter(arr, itms, reference, true/*lastReference*/);
};

/**
 * Tells if ALL items pass the test.
 *
 * @param array 	arr
 * @param function 	callback
 *
 * @return bool
 */
const _all = function(arr, callback) {
	return arr.reduce((prevTest, itm) => prevTest && callback(itm), true);
};

/**
 * Tells if ANY items pass the test.
 *
 * @param array 	arr
 * @param function 	callback
 *
 * @return bool
 */
const _any = function(arr, callback) {
	return arr.reduce((prevTest, itm) => prevTest || callback(itm), false);
};

/**
 * Calls callback with each item in the list and waits inbetween for each callback's promise to resolve.
 * The delay parameter may be used solely as the wait or as an addition to the promise-based wait.
 *
 * @param int|string|object 	interval
 *
 * @return new Promise
 */
const _inSequence = function(arr, callback, timing) {
	if (!_isArray(arr)) {
		return;
	};
	if (_isObject(timing)) {
		var sync = timing.sync || false;
		var delay = timing.delay || 0;
		var delayAlways = timing.delayAlways || false;
	} else {
		var sync = _isBoolean(timing) || timing === 'last' ? timing : false;
		var delay = _isNumber(timing) ? timing : 0;
		var delayAlways = false;
	};
	var items = arr;
	var promise = new Promise(function(resolve, reject) {
		if (items.length) {
			var call = function(i) {
				var advance = () => {
					// Delay first before asking items.length
					// Good for progressivly adding new items
					// -----------------------
					if (delayAlways) {
						if (delay) {
							setTimeout(() => {
								if (items.length > i + 1) {
									call(i + 1);
								} else {
									resolve()
								};
							}, delay);
						} else {
							if (items.length > i + 1) {
								call(i + 1);
							} else {
								resolve()
							};
						};
					// Ask items.length first before delay
					// Good for when delay should only be BETWEEN items
					// -----------------------
					} else {
						if (items.length > i + 1) {
							if (delay) {
								setTimeout(() => {call(i + 1);}, delay);
							} else {
								call(i + 1);
							};
						} else {
							resolve()
						};
					};
				};
				var ret = callback(items[i]);
				if (ret instanceof Promise && (sync === true || (sync === 'last' && i === items.length - 1))) {
					// On any of the outcomes...
					ret.then(advance);
					ret.catch(advance);
				} else {
					advance();
				};
			};
			call(0);
		} else {
			resolve();
		};
	});
	return promise;
};

/**
 * @export
 */
export {
	_from,
	_first,
	_last,
	_avg,
	_max,
	_min,
	_rand,
	_sum,
	_slice, 
	_preceding,
	_following,
	_precedingLast,
	_followingLast,
	_between,
	_intersect,
	_difference,
	_filter,
	_divide,
	_flatten,
	_sort,
	_unique,
	_pushUnique,
	_concatUnique,
	_remove,
	_exclude,
	_replace,
	_concatBefore,
	_concatAfter,
	_concatBeforeLast,
	_concatAfterLast, 
	_all,
	_any,
	_inSequence
};
